/*Cajas*/
.box-i{
    width: 450px;
    height: 200px;
}
/*botones*/

.btn-primary{
    background-color: rgba(238, 232, 225, 1);
    height: 40px;
    width: 450px;
    border: 0;
    font-size: 24px;
    margin-top:20px
}
.center-btn{
    text-align: center;
}
/*Colores*/
.bg-secondary{
    background-color: rgba(238, 232, 225, 1);
}

/* Margenes */
.mt-s0{
    margin-top:20px;
}
.mt-o{
    margin-top:3%;
}

.mt-1{
    margin-top:5%;
}
.mt-2{
    margin-top:10%;
}
.mt-3{
    margin-top:15%;
}
.mt-4{
    margin-top:20%;
}
.my-auto{
    margin-left: auto !important;
    margin-right: auto !important;
}

.mb-0{
    margin-bottom: 5%;
}
/*Alineaciones texto-etc*/
.h-align-center{
    text-align: center;
}
/*Transfos de textos*/
.i-tittle{
    font-size: 24px;
    color:#3B6978;
    font-style: italic;
}
/*Especificos de tags*/
textarea{
    resize: none;
    border: none;
    font-size: 16px;
    
}
html,body{
    background-color: #E7DFD5;
    font-family: Arvo;
}


/*Responsive changes*/

@media (max-width:500px){
    .box-i{
        width: 250px;
        height: 100px;
    }

    .btn-primary{
        background-color: rgba(238, 232, 225, 1);
        height: 40px;
        width: 250px;
        border: 0;
        font-size: 24px;
        margin-top:20px
    }
    .mt-1{
        margin-top:30%
    }
    .mt-2{
        margin-top:100px;
    }
    
}

@media (min-width:501px) and (max-width:800px){
    .box-i{
        width: 250px;
        height: 100px;
    }

    .btn-primary{
        background-color: rgba(238, 232, 225, 1);
        height: 40px;
        width: 250px;
        border: 0;
        font-size: 24px;
        margin-top:20px
    }
    .mt-1{
        margin-top:20%
    }
    .mt-2{
        margin-top:150px;
    }
    
}

@media (max-width:1400px) {
    .mb-0{
        margin-bottom: 8%;
    }
}